import { RouterModule } from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FontAwesomeModule} from '@fortawesome/angular-fontawesome';
import {FormsModule} from '@angular/forms';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { IndexComponent } from './index/index.component';
import {NgxTypedJsModule} from 'ngx-typed-js';
import { HomeComponent } from './home/home.component';
import { POCComponent } from './poc/poc.component';
import { FooterComponent } from './footer/footer.component';
import { SmartOnComponent } from './smart-on/smart-on.component';
import { TrackAllComponent } from './track-all/track-all.component';
import { AboutComponent } from './about/about.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {MatStepperModule} from '@angular/material/stepper';
import { MAT_STEPPER_GLOBAL_OPTIONS } from '@angular/cdk/stepper';
import {MatIconModule} from '@angular/material/icon';
import {MatGridListModule} from '@angular/material/grid-list';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { EmbedVideo } from 'ngx-embed-video';
import { SliderModule } from 'angular-image-slider';
import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';
import { LoadingScreenComponent } from './loading-screen/loading-screen.component';
import { LoadingScreenInterceptor } from './loading-screen/loading-screen.interceptor';

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    IndexComponent,
    HomeComponent,
    POCComponent,
    FooterComponent,
    SmartOnComponent,
    TrackAllComponent,
    AboutComponent,
    LoadingScreenComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    EmbedVideo.forRoot(),
    AppRoutingModule,
    NgxTypedJsModule,
    MatIconModule,
    MatStepperModule,
    MatGridListModule,
    MatProgressSpinnerModule,
    FontAwesomeModule,
    RouterModule.forRoot([
      {path: '', component: HomeComponent },
      {path:'POC', component: POCComponent},
      {path:'smartOn', component: SmartOnComponent},
      {path:'trackAll', component: TrackAllComponent},
      {path:'AboutUs', component: AboutComponent}
    ]),
    BrowserAnimationsModule,
    SliderModule
  ],
  providers: [{

    provide: MAT_STEPPER_GLOBAL_OPTIONS, useValue: { displayDefaultIndicatorType: false }
  },
  {
    provide: HTTP_INTERCEPTORS,
    useClass: LoadingScreenInterceptor,
    multi: true
  }],
  bootstrap: [AppComponent]
})
export class AppModule { }
